
.uk-card{margin-top: 10%;border-radius: 6px;
}
h5,h4{
  font-family: Ubuntu;
  text-transform: capitalize;
  margin-top: 8px;
}
h4{
  font-weight: 300;
  line-height: 1.8;
  margin-left: 20px;
}
.box-shadow{
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
.box-shadow:hover{
  opacity: 0.8;
}
.uk-h3{
  font-family: Ubuntu;
  font-weight: 100;
  text-transform: capitalize;
  letter-spacing: 1px;
}
.view-button{
  font-family: 'Ubuntu', sans-serif !important;
  text-transform: capitalize;
  font-weight: 100;
  font-size: 16px;
  letter-spacing: 2px;
  border: none;
  height: 50px;
  padding: 0 50px !important;
  border-radius: 3px;
  color: #fff;
  line-height: 50px;
  text-decoration: none;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.view-button:hover{color: #fff
}
.green-bg{  background: #4CAF50;
}
.green-bg:hover{  background: #388E3C;
}
.purple-bg{  background: #9C27B0;
}
.purple-bg:hover{  background: #7B1FA2;
}
.red-bg{  background: #F44336; cursor: not-allowed !important;
}



