
.profile-image{
  width: 200px;
  border: 5px solid #333;
  border-radius: 50%;
}
.uk-card{
  border-radius: 6px;
  margin-top: 5%;
}
.name{
  font-family: 'Ubuntu', sans-serif !important;
  color: #000;
  font-weight: 100;
  letter-spacing: 1px;
  margin: 10px;
  text-transform: capitalize;
}
h5{
  font-family: 'Ubuntu', sans-serif !important;
  color: #000;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 20px;
}
h3{
  font-family: 'Ubuntu', sans-serif !important;
  color: #333;
  font-weight: 100;
  letter-spacing: 1px;
  margin: 20px;
  text-transform: capitalize;
  display: inline-block;
  float: left;
}
.view-icons{
  padding: 10px;
  color: #333;
}
.git:hover{
  color: #6e5494 !important;
}
.insta:hover{
  color: #e1306c !important;
}
.tweet:hover{
  color: #1da1f2 !important;
}



